     <span class="token string single-quoted-string">'suite_secret'</span>         <span class="token operator">=></span> <span class="token string single-quoted-string">'应用secret'</span><span class="token punctuation">,</span>
     <span class="token string single-quoted-string">'token'</span>                <span class="token operator">=></span> <span class="token string single-quoted-string">'应用的Token'</span><span class="token punctuation">,</span>
